 export default class AvatarLoader {
    static loadAvatar(photoUrl: string, spriteNode: cc.Node) {
        if (!photoUrl) {
            console.warn("No photo URL provided.");
            return;
        }

        // Ensure the node has a Sprite component
        const sprite = spriteNode.getComponent(cc.Sprite);
        if (!sprite) {
            console.error("Provided node does not have a Sprite component.");
            return;
        }

        //photoUrl = "https://t.me/i/userpic/320/2uptPMrsaIXViYtEoOWxkkLiDexwDDkQySVmeA8FsxU.svg";

        // Load image from the URL
        cc.assetManager.loadRemote(photoUrl, (err, texture: cc.Texture2D) => {
            if (err) {
                console.error("Failed to load avatar:", err);
                return;
            }

            // Apply the texture to the sprite
            sprite.spriteFrame = new cc.SpriteFrame(texture);
        });
    }


    static createAvatarImage(url: string, parent: cc.Node) {
        const imgElement = document.createElement('img');
        imgElement.src = url;
        imgElement.style.position = 'absolute';
        imgElement.style.width = '100px'; // Adjust size as needed
        imgElement.style.height = '100px';
        imgElement.style.left = '50px';  // Adjust position as needed
        imgElement.style.top = '50px';
    
        // Attach to document
        document.body.appendChild(imgElement);
    
        // Remove when not needed
        parent.once('destroy', () => {
            document.body.removeChild(imgElement);
        });
    };


    static loadTextureFromImage(url: string, sprite: cc.Sprite){

        const imgElement = document.createElement('img');
        imgElement.src = url;
        //imgElement.style.visibility = 'none';
        imgElement.style.position = 'absolute';
        imgElement.style.width = '100px'; // Adjust size as needed
        imgElement.style.height = '100px';
        imgElement.style.left = '50px';  // Adjust position as needed
        imgElement.style.top = '50px';

        imgElement.onload = () =>{
            const texture = new cc.Texture2D();
            texture.initWithElement(imgElement);
            texture.handleLoadedTexture();
    
            // Step 3: Create a SpriteFrame from the Texture2D
            const spriteFrame = new cc.SpriteFrame();
            spriteFrame.setTexture(texture);
    
            // Step 4: Assign the SpriteFrame to the provided Sprite component
            sprite.spriteFrame = spriteFrame;
        }

        imgElement.onerror = (error) => {
            console.error("Failed to load image:", error);
        };

    };
}