const { ccclass, property } = cc._decorator;

@ccclass
export default class DailyTaskItem extends cc.Component {

    @property(cc.Label)
    public label_name: cc.Label | null = null;

    @property(cc.Label)
    public label_reward: cc.Label | null = null;

    @property(cc.Button)
    public btn: cc.Button | null = null;

    @property(cc.Node)
    public task_done: cc.Node | null = null;

    @property(cc.Node)
    public task_not_done: cc.Node | null = null;

    @property(cc.Sprite)
    public fill: cc.Sprite = null;

    show() {
        this.node.active = true;
    }

    hide() {
        this.node.active = false;
    }

    init(name: string, reward: number){
        this.label_name.string = name;
        this.label_reward.string = reward.toString();
    }

    setFill(fill: number){
        this.fill.fillRange = fill;

        if(fill >= 1){
            this.task_not_done.active = false;
        }
    }

}