const monetagLoadPromise = new Promise<boolean>((resolve, reject) => {
    if (cc.sys.platform === cc.sys.MOBILE_BROWSER || cc.sys.platform === cc.sys.DESKTOP_BROWSER) {
        const script = document.createElement("script");
        script.src='//niphaumeenses.net/vignette.min.js';
        script.dataset.zone = '8721959';
        script.dataset.sdk = 'show_8721959';
        script.async = true;
        script.onload = () => {
            resolve(true);
        };
        script.onerror = () => reject(new Error("Unable to load Monetag SDK, please check logs."));
        document.head.appendChild(script);
    }
});


@ccclass('MonetagController')
export class MonetagController {
    private static _instance: MonetagController;
    private constructor() {

    }

    public static get Instance(): MonetagController {
        if (!MonetagController._instance) {
            MonetagController._instance = new MonetagController();
        }
        return MonetagController._instance;
    }

    private _monetagJS: boolean = null;

    public async init(): Promise<{ success: boolean }> {
        this._monetagJS = await monetagLoadPromise;

        if (this._monetagJS) {
            return Promise.resolve({ success: true });
        } else {
            return Promise.resolve({ success: false });
        }
    }

    public showFullscreenAd(){
        UserController.Instance?.patchAd();

        (window as any).show_8721959().then(() => {
            // You need to add your user reward function here, which will be executed after the user watches the ad.
            // For more details, please refer to the detailed instructions.
            console.log('You have seen ad ad!');
        })
    }

    public showFullscreenAdRewarded() : Promise<any> {
        UserController.Instance?.patchAd();
        return (window as any).show_8721959();
    }
}