export function vibrate(){
    if (navigator.vibrate) {
        navigator.vibrate(100);
    } 
}

// Випадкове ціле число
export function random(lower: number, upper: number): number {
    return Math.floor(Math.random() * (upper - lower + 1)) + lower;
}

// Перемішування масиву
export function shuffle(arr: any[]) {
    let length: number = arr.length,
        randomIndex: number,
        temp: any;
    while (length) {
        randomIndex = Math.floor(Math.random() * (length--));
        temp = arr[randomIndex];
        arr[randomIndex] = arr[length];
        arr[length] = temp;
    }
    return arr;
}

// Сортування об'єктів у масиві
export function sort(arr: any[] | unknown, key: any, flag: boolean = true) {
    if (arr instanceof Array) {
        return arr.sort((a, b) => {
            if (a[key] > b[key]) {
                return flag ? 1 : -1;
            } else if (a[key] < b[key]) {
                return flag ? -1 : 1;
            } else {
                return 0;
            }
        });
    }
}

// Сортування рядків, що містять числа, на основі числових значень
export function sortSpriteNameByNum(frames: cc.SpriteFrame[]) {
    const getNumberInSpriteName = (name: string) => {
        const reg = /\d+/g;
        return parseInt(name.match(reg)[0] || '0');
    };
    return frames.sort((a, b) => getNumberInSpriteName(a.name) - getNumberInSpriteName(b.name));
}

// Перетворення секунд
export function formatSeconds(seconds: number | string, dateFormat = 'h:i:s'): string {
    seconds = Number(seconds);
    let obj: any = {};
    obj.h = Number.parseInt(String(seconds / 3600));
    obj.i = Number.parseInt(String((seconds - obj.h * 3600) / 60));
    obj.s = Number.parseInt(String(seconds - obj.h * 3600 - obj.i * 60));
    if (obj.h < 10) obj.h = '0' + obj.h;
    if (obj.i < 10) obj.i = '0' + obj.i;
    if (obj.s < 10) obj.s = '0' + obj.s;
    // Парсинг
    var rs = dateFormat.replace('h', obj.h).replace('i', obj.i).replace('s', obj.s);
    return rs;
}

// Відстань між двома точками
export function getDistance(start: cc.Vec2, end: cc.Vec2) {
    const pos = cc.v2(start.x - end.x, start.y - end.y);
    const dis = Math.sqrt(pos.x * pos.x + pos.y * pos.y);
    return dis;
}

// Кут між двома точками
export function getAngle(start: cc.Vec2, end: cc.Vec2) {
    // Обчислення напряму
    const dx = end.x - start.x;
    const dy = end.y - start.y;
    const dir = cc.v2(dx, dy);
    // Обчислення кута в радіанах
    const angle = dir.signAngle(cc.v2(1, 0));
    // Конвертація радіанів у градуси
    const degree = angle / Math.PI * 180;
    return -degree;
}

// Перетворення координат вузла 1 у вузол 2
export function toXY(node1: cc.Node, node2: cc.Node) {
    const wpos = node1.convertToWorldSpaceAR(cc.v2(0, 0));
    const pos = node2.convertToNodeSpaceAR(wpos);
    return pos;
}

// Генерація посилання на аватар WeChat
export function wxAvatar(avatarUrl: string, isCached: boolean = true) {
    if (isCached) {
        avatarUrl += `?sail.jpg`;
    } else {
        const time = new Date().getTime();
        avatarUrl += `?sail=${time}.jpg`;
    }
    return new Promise<void>((resolve, reject) => {
        cc.loader.load(avatarUrl, function (err: any, texture: any) {
            if (err) reject && reject();
            resolve && resolve(texture);
        });
    });
}

// Генерація UUID
export function uuid() {
    let d = new Date().getTime();
    var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = (d + Math.random() * 16) % 16 | 0;
        d = Math.floor(d / 16);
        return (c == 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
    return uuid;
}


export function getUTCRemainingTimeToEndOfWeek(): string {
    const now = new Date(); // Current local time
    const nowUTC = new Date(
        now.getUTCFullYear(),
        now.getUTCMonth(),
        now.getUTCDate(),
        now.getUTCHours(),
        now.getUTCMinutes(),
        now.getUTCSeconds(),
    ); // Convert to UTC time

    // Get the current day of the week (0 = Sunday, 6 = Saturday)
    const dayOfWeek = nowUTC.getUTCDay();

    // Calculate the number of days until the end of the week (Sunday at 23:59:59 UTC)
    const daysRemaining = 7 - dayOfWeek;

    // Calculate the time of the next Sunday at 23:59:59 UTC
    const endOfWeek = new Date(
        nowUTC.getUTCFullYear(),
        nowUTC.getUTCMonth(),
        nowUTC.getUTCDate() + daysRemaining,
        23, // 23 hours
        59, // 59 minutes
        59, // 59 seconds
    );

    // Calculate the time difference in milliseconds
    const timeDifference = endOfWeek.getTime() - nowUTC.getTime();

    // Convert the difference into days, hours, minutes, and seconds
    const seconds = Math.floor((timeDifference / 1000) % 60);
    const minutes = Math.floor((timeDifference / (1000 * 60)) % 60);
    const hours = Math.floor((timeDifference / (1000 * 60 * 60)) % 24);
    const days = Math.floor(timeDifference / (1000 * 60 * 60 * 24));

    // Format the time as dd:hh:mm
    return `${String(days).padStart(2, '0')}:${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
}

export function getUTCRemainingTimeToEndOfWeekToSeconds(): string {
    const now = new Date(); // Current local time
    const nowUTC = new Date(
        now.getUTCFullYear(),
        now.getUTCMonth(),
        now.getUTCDate(),
        now.getUTCHours(),
        now.getUTCMinutes(),
        now.getUTCSeconds(),
    ); // Convert to UTC time

    // Get the current day of the week (0 = Sunday, 6 = Saturday)
    const dayOfWeek = nowUTC.getUTCDay();

    // Calculate the number of days until the end of the week (Sunday at 23:59:59 UTC)
    const daysRemaining = 7 - dayOfWeek;

    // Calculate the time of the next Sunday at 23:59:59 UTC
    const endOfWeek = new Date(
        nowUTC.getUTCFullYear(),
        nowUTC.getUTCMonth(),
        nowUTC.getUTCDate() + daysRemaining,
        23, // 23 hours
        59, // 59 minutes
        59, // 59 seconds
    );

    // Calculate the time difference in milliseconds
    const timeDifference = endOfWeek.getTime() - nowUTC.getTime();

    // Convert the difference into days, hours, minutes, and seconds
    const seconds = Math.floor((timeDifference / 1000) % 60);
    const minutes = Math.floor((timeDifference / (1000 * 60)) % 60);
    const hours = Math.floor((timeDifference / (1000 * 60 * 60)) % 24);
    const days = Math.floor(timeDifference / (1000 * 60 * 60 * 24));

    // Format the time as dd:hh:mm:ss
    return `${String(days).padStart(2, '0')}:${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
}

export async function generateGameToken(userId: number, timestamp: number, secret: string): Promise<string> {
    if (!secret) throw new Error('GAME_SECRET_KEY not configured');
    const data = `${userId}-${timestamp}-${secret}`;
    return await createHash(data);
}

export async function createHash(message): Promise<string> {
    const encoder = new TextEncoder();
    const data = encoder.encode(message);
    const hashBuffer = await crypto.subtle.digest('SHA-256', data);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
    return hashHex;
}

