import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";
import PoolManager from "../manager/PoolManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import LeaderboardItem from "../ui/LeaderboardItem";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import { TestManager } from "../api/TestManager";
import { UserController } from "../api/controllers/AuthController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class LeaderboardLayer extends BaseLayer {

    youItem: LeaderboardItem = null

    btnWinners: cc.Node = null
    btnFindMe: cc.Node = null

    winners: cc.Node = null
    winnersPanel: cc.Node = null
    winnersContent: cc.Node = null
    btnCloseWinners: cc.Node = null

    //bottomPanel
    btnMain: cc.Node = null
    btnLeaderboard: cc.Node = null
    btnTasks: cc.Node = null
    btnProfile: cc.Node = null

    content: cc.Node = null



    onLoad() {
        this.content = cc.find('main/scroll_view/view/content', this.node)
        this.youItem = cc.find('main/leaderboard_item_you', this.node).getComponent(LeaderboardItem);
        this.btnMain = cc.find('tabs_panel/btn_main', this.node)
        this.btnLeaderboard = cc.find('tabs_panel/btn_leader', this.node)
        this.btnTasks = cc.find('tabs_panel/btn_tasks', this.node)
        this.btnProfile = cc.find('tabs_panel/btn_profile', this.node)

        this.btnFindMe = cc.find('btn_find_me', this.node)
        this.btnWinners = cc.find('btn_winners', this.node)
        this.winners = cc.find('winners', this.node)
        this.winnersPanel = cc.find('panel', this.winners)
        this.btnCloseWinners = cc.find('btn_close', this.winnersPanel)


        this.btnMain.on('click', this.onMainClick, this)
        this.btnLeaderboard.on('click', this.onLeaderboardClick, this)
        this.btnTasks.on('click', this.onTasksClick, this)
        this.btnProfile.on('click', this.onProfileClick, this)

        this.btnFindMe.on('click', this.onFindMeClick, this)
        this.btnWinners.on('click', this.onWinnersClick, this)
        this.winnersPanel.on('click', this.onCloseWinnersClick, this)
        this.btnCloseWinners.on('click', this.onCloseWinnersClick, this)

    }

    onDestroy() {
        this.btnMain.off('click', this.onMainClick, this)
        this.btnLeaderboard.off('click', this.onLeaderboardClick, this)
        this.btnTasks.off('click', this.onTasksClick, this)
        this.btnProfile.off('click', this.onProfileClick, this)

        this.btnFindMe.off('click', this.onFindMeClick, this)
        this.btnWinners.off('click', this.onWinnersClick, this)
        this.winnersPanel.off('click', this.onCloseWinnersClick, this)
        this.btnCloseWinners.off('click', this.onCloseWinnersClick, this)
    }

    onEnable() {
        this.winners.active = false;

        const youItem = this.youItem;
        const contentNode = this.content;
        const poolManager = PoolManager.instance;

        UserController.Instance?.getLeaderboard().then((response) => {
           
            const you = response.user_position;
            youItem.init(you.position, `@${you.username}`, you.points);
            const players = response.leaderboard;
            contentNode.destroyAllChildren();

            for (let i = 0; i < players.length; i++) {
                const player = players[i];
                const item = player.user_id == you.user_id ? poolManager.getNode(`leaderboard_item_you`, contentNode) : poolManager.getNode(`leaderboard_item`, contentNode);
                const leaderboardItem = item.getComponent(LeaderboardItem);
                leaderboardItem.init(player.position, `@${player.username}`, player.points);
            }

        }).catch((error) => {
            console.error("Failed to fetch leaderboard:", error);
        });

    }

    onDisable() { }

    onMainClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
    }

    onLeaderboardClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false) 
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD)
    }

    onTasksClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS)
    }

    onProfileClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE)
    }

    onFindMeClick() { AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) }

    onWinnersClick() { 
        // AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        // this.winners.active = true;

        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.HISTORY)
    }


    onCloseWinnersClick() { 
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) 
        this.winners.active = false;
    }
    
}
